.bundle {
  color: $color-font-secondary;
  text-align: left;

  display: flex;
  flex-wrap: wrap;
  align-content: flex-start;

  width: 368px;
  min-height: 670px;

  padding-bottom: 31px;

  border-radius: 5px;

  box-shadow: 0 0 0 1px $color-font-primary-low-opac;

  background-color: $color-font-contrast;

  &--hit {
    width: 367px;
    min-height: 666px;

    box-shadow: 0 0 0 3px $color-accent;
  }
}

.bundle__name {
  font-weight: 600;
  font-size: 28px;
  line-height: 34px;
  letter-spacing: -1.27273px;
  opacity: 0.7;

  margin-top: 41px;
  margin-left: 29px;

  &--hit {
    color: $color-accent;
    letter-spacing: normal;
    opacity: 1;

    margin-top: 38px;
  }
}

.bundle__price {
  font-weight: 600;
  font-size: 25px;
  line-height: 34px;
  opacity: 0.7;
  text-align: right;

  margin-top: 43px;
  margin-right: 29px;
  margin-left: auto;
  margin-bottom: 38px;

  &--hit {
    opacity: 1;
    color: $color-accent;

    margin-top: 39px;
    margin-right: 28px;
    margin-bottom: 41px;
  }
}

.bundle__tip {
  font-size: 16px;
  line-height: 24px;

  padding-left: 30px;
  padding-right: 50px;
  margin-top: 0;
}

.bundle__list {
  list-style: none;

  position: relative;

  margin-top: 42px;
  margin-bottom: 14px;

  &::before {
    content: "";

    position: absolute;
    top: -32px;
    left: 29px;

    height: 1px;
    width: 310px;

    background-color: $color-font-primary-low-opac;
  }

  &--hit {
    display: flex;
    flex-direction: column;
    justify-content: space-between;

    height: 291px;

    margin-top: 43px;
    margin-bottom: 54px;

    &::before {
      opacity: 0.5;

      top: -32px;

      background-color: $color-accent;
    }
  }
}

.bundle__list-item {
  font-size: 16px;
  line-height: 32px;

  position: relative;

  padding-left: 20px;
  padding-right: 30px;
  margin-bottom: 15px;

  &::before {
    content: "";

    position: absolute;
    top: 5px;
    left: -15px;

    height: 22px;
    width: 22px;

    background-image: url(../img/icon-included.svg)
  }

  &--hit {
    margin-top: 0;
    margin-bottom: 0;

    &:nth-of-type(2) {
      margin-top: -8px;

      &::before {
        top: 4px;
      }
    }

    &:nth-of-type(3)::before {
      top: 4px;
    }

    &::before {
      top: 6px;
      left: -11px;

      background-image: url(../img/icon-included-hit.svg)
    }
  }
}

.bundle__button {
  text-align: center;

  width: 310px;

  align-self: flex-end;

  margin: auto auto 0;
}

@media (max-width: $tablet-only-width) {
  .bundle {
    text-align: center;

    flex-direction: column;

    min-height: 628px;
    width: 286px;

    padding-bottom: 0;
    margin-left: 52px;

    &--hit {
      min-height: 620px;

      margin-left: 72px;

      border-radius: 5px 5px 0 0;
    }
  }

  .bundle__name {
    font-size: 24px;

    margin-top: 30px;
    margin-left: 0;
    margin-bottom: 0;
  }

  .bundle__price {
    font-weight: 600;
    opacity: 0.7;
    text-align: center;

    margin-top: 10px;
    margin-right: 114px;
    margin-bottom: 0px;

    &--hit {
      margin-right: auto;
    }
  }

  .bundle__tip {
    margin-top: 12px;
    padding-left: 20px;
    padding-right: 20px;
  }

  .bundle__list {
    margin-top: 13px;
    margin-bottom: 0;

    &::before {
      top: -22px;
      left: 19px;

      width: 248px;
    }

    &--hit {
      height: 324px;
    }
  }

  .bundle__list-item {
    text-align: left;

    padding-left: 9px;
    padding-right: 20px ;
    margin-bottom: 10px;

    &::before {
      left: -25px;
    }

    &--hit {
      &::before {
          left: -21px;
      }

      &:nth-of-type(2)::before {
        top: 5px;
      }

      &:nth-of-type(4)::before {
        top: 4px;
      }
    }
  }

  .bundle__button {
    width: 288px;

    margin-left: -1px;
  }
}

@media (max-width: $mobile-only-width) {
  .bundle {
    margin-left: 0;

    &--hit {
      margin-left: 0;

    }
  }

  .bundle__name {

  }

  .bundle__price {

  }

  .bundle__tip {

  }

  .bundle__list {

    &::before {

    }

    &--hit {
      &::before {

      }
    }
  }

  .bundle__list-item {
    &::before {

    }

    &--hit {
        &::before {
      }
    }
  }
}
